.select {
	position: relative;
	font-size: 12px;
	display: inline-flex;


	&.is-active {
		.select__body {
			transform: rotateX(0);
			visibility: visible;
			opacity: 1;
			transition: transform .3s linear;
		}
		.select__header {
			&::after {
				transform: rotate(180deg);
			}
		}

	}

	&__header {
		position: relative;
		cursor: pointer;
		display: flex;
		align-items: center;
		padding: 0 10px 0 7px;
		border-radius: 8px;
		border: 1px solid #9A9A9A;
		min-width: 79px;
		height: 28px;
		gap: 10px;
		span {
			position: absolute;
			font-size: 11px;
			top: 0;
			left: 0;
			transform: translate(-50%, -60%);
			min-width: 59px;
			background-color: #fff;
		}
		&::after {
			min-width: 10px;
			height: 6px;
			background: url("../img/icon/arr/arr-select.svg");
			content: '';
		}
	}

	&__body {
		padding: 14px 0;
		background-color: #fff;
		border: 1px solid #cccccc;
		border-radius: 8px;
		 left: 0;
		 position: absolute;
		 top: 28px;

		width: 140px;
		z-index: 5;
		transform: rotateX(-90deg);
		transform-origin: 0 0;
		visibility: hidden;
		opacity: 0;

		&::after {
			display: none;
		}

		input {
			-webkit-appearance: none;
			appearance: none;
			position: absolute;
		}
		&-item {
			display: flex;
			align-items: center;
			cursor: pointer;
			padding: 7px 14px;
			width: 100%;
			span {
				width: 130px;
			}
			&.active{
				&::after {
					min-width: 11px;
					height: 8px;
					background: url("../img/icon/arr/arr-select-green.svg");
					content: '';
				}

			}

			&:hover {
				background-color: #B4DFFB;
			}
		}
	}




}
