.page-basket {
	padding: 20px;

	&-title {
		margin-bottom: 50px;
		display: flex;
		align-items: flex-end;
		gap: 20px;
		&__quan {
			line-height: 1.4;
		}

	}

	&-tab {
		.tab__nav {
			display: flex;
			gap: 42px;
			padding-bottom: 56px;
			font-size: 16px;
			font-weight: 400;
		}
		.tab__link {
			min-width: 150px;
			min-height: 45px;
			display: flex;
			align-items: center;
			justify-content: center;
			&--active {
				color: #000;
				background: #ECECEC;
				border-radius: 5px;
				border-bottom: 3px solid red;
			}
		}
		.tab__content-item {
			display: flex;
			gap: 40px;
		}


		&__left {
			flex: 1 1 auto;
			&-bar {
				padding-bottom: 16px;
				min-height: 20px;
				border-bottom: 1px solid #EAEAEA;
				display: flex;
				gap: 38px;
				align-items: center;
				margin-bottom: 33px;
				label {
					span {
						gap: 12px;
						&::before {
							width: 20px;
							height: 20px;
						}
					}

				}

				&-del {
					display: flex;
					gap: 5px;
					align-items: center;
					&::before {
						min-width: 20px;
						min-height: 20px;
						background: url("../img/icon/del-basket-bar.svg");
						content: '';
					}
				}
			}


		}

		&__right {
			position: sticky;
			top: 20px;
			width: 317px;
			height: 10px;
			margin-bottom: 600px;

			.btn {
				font-size: 16px;
				font-weight: 500;
				width: 100%;
				height: 65px;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-bottom: 25px;
				border-radius: 10px;
			}

			&-spec {
				border: 1px solid #EAEAEA;
				background: linear-gradient(0deg, #EAEAEA, #EAEAEA),
				linear-gradient(0deg, #F4F4F4, #F4F4F4);
				padding: 20px;
				border-radius: 15px;
				margin-bottom: 25px;


				&__li {
					font-size: 14px;
					line-height: lh(14,30);
				}

				&__total {
					display: flex;
					justify-content: space-between;
					align-items: flex-end;
					margin-top: 12px;
					border-top: 1px solid #D0D0D0;
					padding-top: 17px;
					font-size: 20px;
					span {
						font-size: 24px;
						font-weight: 700;
					}

				}
			}

			&-info {
				background: #D0EDFF;
				padding: 20px;
				border-radius: 15px;
				margin-bottom: 25px;
				font-size: 12px;
				line-height: lh(12,24);
			}

		}

	}


}
