.page-product-card {
	.banner {
		margin-bottom: 36px;
	}

	h1 {
		color: #000;
		font-size: 24px;
		font-style: normal;
		font-weight: 700;
		line-height: lh(24,22);
		padding-bottom: 30px;
		border-bottom: 1px solid #CDCDCD;
		margin-bottom: 22px;
	}

	.product {
		margin-bottom: 3px;
		&__box {
			display: flex;

			.slider {
				flex-grow: 1;
				max-width: 522px;
				width: 400px;
				.gallery-slider {
					max-width: 400px;


					&__large-img {
						cursor: pointer;
						height: 400px;
						display: flex;
						align-items: center;
						justify-content: center;
						margin-bottom: 35px;
					}

					&__small {
						position: relative;
						overflow: hidden;
						width: 270px;
						margin: 0 auto;

						&-img {
							display: flex;
							justify-content: space-between;
							position: relative;
							gap: 10px;

							.el {
								cursor: pointer;
								height: 60px;
								max-width: 60px;
								min-width: 60px;
								transition: .3s;
								opacity: .5;
								padding: 5px;
								border: 2px solid transparent;
								display: flex;
								align-items: center;
								justify-content: center;
								&.active {
									border: 2px solid #F90;
									opacity: 1;
								}
							}
						}
						&-prev, &-next {
							position: absolute;
							top: 50%;
							width: 30px;
							height: 30px;
							background: rgba(#303030, .8);
							border-radius: 100%;
							display: flex;
							align-items: center;
							justify-content: center;
							z-index: 2;
							&::before {
								width: 9px;
								height: 9px;
								border-bottom: 2px solid $accent;
								border-left: 2px solid $accent;
								content: '';
							}

							cursor: pointer;
							&:hover {
								opacity: .8;
							}
						}
						&-prev {
							left: 10px;
							transform: translateY(-50%) rotate(45deg);
						}
						&-next {
							right: 20px;
							transform: translateY(-50%) rotate(-130deg);
						}
					}

				}


			}

			.specification {
				width: 400px;
				p {
					color: #666;
					line-height: 27px;
					margin-bottom: 6px;
				}

				&__size-list {
					display: flex;
					gap: 10px;
					margin-bottom: 19px;
					a {
						display: flex;
						align-items: center;
						justify-content: center;
						min-width: 50px;
						height: 46px;
						border-radius: 20px;
						border: 3px solid #999;
						font-size: 18px;
						font-weight: 700;
						line-height: lh(18,27);

						&.active {
							border: 3px solid #F90;
						}
					}
				}


				&__color-list {
					display: flex;
					gap: 16px;
					margin-bottom: 26px;
					img {
						z-index: -1;
					}
					a {
						display: flex;
						align-items: center;
						justify-content: center;
						min-width: 60px;
						height: 60px;
						border-radius: 60px;
						border: 3px solid #999;
						&.active {
							border: 3px solid #F90;
						}
					}
				}

				&__list{
					color: #000;
					line-height: lh(14,27);
					li {
						display: flex;

						span {
							background-color: #fff;
							position: relative;
							&:nth-child(1) {
								width: 220px;
								position: relative;
								&::after {
									position: absolute;
									bottom: 7px;
									left: 0;
									width: 100%;
									height: 3px;
									background: url("../img/icon/point.png");
									content: '';
									z-index: 0;
								}
								strong {
									position: relative;
									z-index: 2;
									background-color: white;
									font-weight: 400;
								}
							}

						}

					}
					a {
						color: #0066FF;
					}

				}


			}

			.right-box {
				margin-left: auto;
				width: 350px;

				&__link {
					display: flex;
					justify-content: flex-end;
					padding-right: 30px;
					gap: 37px;
					margin-bottom: 61px;
					a {
						color: #333;
						line-height: lh(14, 22);
						position: relative;
						display: flex;
						gap: 12px;






					}

					&__favorites {

						&::before {
							width: 19px;
							height: 17px;
							background: url(../img/icon/heart-hit.svg);
							margin-left: auto;
							cursor: pointer;
							transition: all 0.5s;
							content: '';
						}

						&:hover {
							&::before {
								background: url(../img/icon/heart-orahge.svg);
							}
						}
					}

					&__compare {
						&::before {
							width: 14px;
							height: 18px;
							background: url(../img/icon/bar-hit.svg);
							cursor: pointer;
							transition: all 0.5s;
							content: '';
						}

						&:hover {
							opacity: 1;
							&::before {
								background: url(../img/icon/bar-orange.png);
							}
						}
					}
				}

				&__inner {
					padding: 30px 37px 23px 37px;
					border-radius: 15px;
					background: #EAEDE9;
					position: relative;
					margin-bottom: 6px;
					&-sale {
						position: absolute;
						top: -9px;
						right: -9px;
						border-radius: 10px;
						background: #FB1A1A;
						color: #FFF;
						font-size: 24px;
						font-weight: 700;
						padding: 9px 16px;
					}
					&-price {
						display: flex;
						gap: 14px;
						font-size: 28px;
						font-weight: 700;
						line-height: lh(28,22);
						margin-bottom: 17px;
						span {
							color: #333;
							font-size: 20px;
							font-style: normal;
							font-weight: 400;
							line-height: lh(20, 22);
							position: relative;
							&::before {
								position: absolute;
								top: 50%;
								left: 0;
								width: 100%;
								height: 1px;
								background-color: #333;
								content: '';
							}

						}
					}

					&-assort {
						border-radius: 5px;
						background: #FED0AC;
						padding: 10px;
						margin-bottom: 8px;
					}

					&-price-opt {
						border-radius: 5px;
						background: #B4DFFB;
						padding: 10px;
						margin-bottom: 20px;
					}

					&-price-opt {

					}

					&__qty {
						display: flex;
						gap: 32px;
						margin-bottom: 12px;
						input {
							width: 91px;
							height: 51px;
							font-size: 24px;
							line-height: lh(24,22);
							border-radius: 5px;
							border: 1px solid #9E9E9E;
							padding-left: 20px;
						}
						.btn {
							display: flex;
							align-items: center;
							justify-content: center;
							min-width: 151px;
						}
					}

					&-li {
						line-height: lh(14,18);
						span {
							font-weight: 700;
						}
					}

					&-stock {
						margin-top: 24px;
						font-size: 16px;
						line-height: lh(16,22);
						display: flex;
						align-items: center;
						gap: 11px;
						&::before {
							width: 30px;
							height: 30px;
							border-radius: 100%;
							background: url("../img/icon/check30-30.svg");
							background-color: #339933;
							content: '';
						}


					}

				}

				&__history {

					&.show {
						.item__date {
							transform: rotateX(0);
							visibility: visible;
							opacity: 1;
							z-index: 1000;
							height: initial;
						}
					}

					&__link {
						color: #666;
						font-size: 13px;
						font-weight: 400;
						line-height: lh(13,26);
						span {
							text-decoration: underline;
						}
					}
					&__list {
						display: grid;
						grid-template-columns: repeat(5, 1fr);
						row-gap: 9px;

						.item {
							position: relative;
							background: #EAEDE9;
							width: 70px;
							color: #000;
							text-align: center;
							font-size: 13px;
							font-weight: 500;
							line-height: lh(13,26);

							&.active {
								&::before {
									position: absolute;
									top: -1px;
									left: 50%;
									transform: translateX(-50%);
									width: 25px;
									height: 9px;
									background: url("../img/icon/polygon.svg");
									content: '';
								}

							}

							&__name {
								color: #333;
							}

							&__date {
								transform: rotateX(-90deg);
								transform-origin: 0 0;
								visibility: hidden;
								opacity: 0;
								height: 0;
							}


							&:nth-child(5n + 1) {
								border-radius: 15px 0 0 15px;
								border: none;
								.item__inner {
									border-left: none;
								}
							}

							&:last-child, &:nth-child(5n) {
								border-radius: 0 15px 15px 0;
							}

							&__inner {
								border-left: 1px solid #C7C9C6;
								margin: 17px 0;
								height: calc(100% - 34px);
							}

						}
					}
				}

			}
		}
	}

	.tab {
		margin-bottom: 50px;
		&__nav {
			padding-left: 20px;
			display: flex;
			gap: 48px;
			color: #666;
			font-size: 18px;
			font-weight: 500;
			line-height: lh(18,26);
			padding-bottom: 12px;
			border-bottom: 1px solid #CDCDCD;
			margin-bottom: 45px;
		}
		&__link--active {
			color: #000;
			font-weight: 700;

		}

		.tab__content-item {
			max-width: 1159px;

			.checked__list {

				display: grid;
				grid-template-columns: 1fr auto;


				margin-bottom: 74px;
				row-gap: 18px;
				color: #333;
				font-size: 15px;
				font-weight: 400;

				label {
					span {
						&::before {
							width: 20px;
							height: 20px;
							border-radius: 3px;
							border: 1px solid #CDCDCD;
						}

					}
					strong {
						color: #000;
					}

				}

			}

			.product-box {
				.product-box__result {
					font-size: 15px;
					font-weight: 400;
					line-height: 38px;
					margin-bottom: 9px            ;
				}
				.item-product-line {
					&:nth-child(2) {
						border-top: 1px solid #EAEAEA;
					}

				}
			}
		}

	}



}
