.landing {
	font-family: 'Rubik', sans-serif;

	h2 {
		font-size: 48px;
		font-weight: 500;
		text-align: center;
		margin-bottom: 50px;
	}

	p {
		font-size: 24px;
		font-weight: 300;
		line-height: lh(24,33);
	}

	.btn {
		background: #FFCB00;
		color: #000;
		padding: 18px 32px;
		border-radius: 5px;

	}



	.header {
		padding-top: 33px;
		background: url("../img/landing/screen-1-bg.jpg") center center/cover no-repeat;
		color: #fff;
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		&--fixed {
			padding: 33px 0;
			min-height: initial;
			background: rgba(0, 0, 0, .8 );
			.row-top {
				margin-bottom: 0 !important;
			}
			.body {
				display: none !important;
			}
		}
		.row-top {
			display: flex;
			align-items: center;
			margin-bottom: 160px;

			.logo-box {
				flex-grow: 1;

				max-width: 561px;
				font-size: 14px;
				display: flex;
				gap: 22px;

				.logo {
					max-width: 185px;
					flex-shrink: 0;
				}
				&__text {
					font-family: 'Nato Sans', sans-serif;
					font-weight: 400;
					line-height: 1;
					display: flex;
					flex-direction: column;
					justify-content: space-between;
					span {
						font-size: 12px;
					}
				}

			}




			&__phone-box {
				font-family: 'Nato Sans', sans-serif;
				display: flex;
				flex-direction: column;
				align-items: center;
				justify-content: space-between;
				text-align: left;
				min-height: 50px;

				.phone {
					color: #FFF;
					font-size: 24px;
					font-style: normal;
					font-weight: 700;
					line-height: 20px;
					margin-bottom: 5px;
				}
				span {
					width: 209px;
					font-size: 14px;
					text-decoration: underline;
					cursor: pointer;
				}
			}

			&__user-box {
				margin-left: auto;
				display: flex;
				align-items: center;
				gap: 10px;
				justify-content: space-between;
				max-width: 494px;
				width: 100%;
				font-size: 16px;
			}


		}

		.body {
			height: 100%;
			flex-grow: 1;
			margin-bottom: 25px;
			display: flex;
			flex-direction: column;
			h1 {
				font-size: 48px;
				font-weight: 700;
				line-height: lh(48,56);
				margin-bottom: 165px;
			}
			&__list {
				max-width: 972px;
				width: 100%;
				margin: 0 auto;
				justify-content: space-between;

				display: flex;
				gap: 58px;
				.item {
					display: flex;
					flex-direction: column;
					justify-content: center;
					align-items: center;
					flex-shrink: 0;
					min-width: 200px;
					&__caption {
						font-size: 25px;
						font-weight: 400;
					}

					&__qty {
						font-size: 70px;
						font-weight: 700;

					}

					&__qty-caption {
						font-size: 48px;
						font-weight: 700;

					}

				}
			}

			&__detail {
				font-size: 24px;
				font-weight: 300;
				margin-top: auto;
				display: flex;
				flex-direction: column;
				justify-content: center;
				gap: 24px;
				align-items: center;
				&::after {
					width: 36px;
					height: 17px;
					background: url("../img/landing/arr-top.svg");
					content: '';
				}

			}
		}



	}

	.who-we {
		padding: 20px 0 150px 0;

		h3 {
			font-size: 32px;
			font-weight: 500;
			margin-top: 65px;
			margin-bottom: 50px;
			text-align: center;
		}

		.exclusive {
			margin-bottom: 137px;


			&__list {
				display: flex;
				flex-wrap: wrap;
				justify-content: center;
				column-gap: 80px;
				row-gap: 102px;
				.item {
					padding: 90px 40px 40px 20px;
					flex-basis: 300px;
					box-shadow: 0 4px 4px 0 #00000040;
					background: #F3F3F3;
					border-radius: 5px;
					position: relative;
					&::before {
						position: absolute;
						top: 15px;
						left: -30px;
						width: 254px;
						height: 69px;
						background: url("../img/landing/distr/exclusive/exclusive.png");
						content: '';
					}

					&__logo-box {
						display: flex;
						align-items: center;
						justify-content: center;
						max-height: 43px;
					}
					&__text {
						margin-top: 24px;
						font-weight: 300;
						font-style: italic;
						font-size: 16px;
						line-height: lh(16, 22);
					}



				}

			}
		}

		.official {
			&__list {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				.item {
					padding: 81px 40px 75px 20px;
					flex-basis: 300px;
					box-shadow: 0px 4px 15px 0px #0000001A;
					border-radius: 5px;
					position: relative;

					&::before {
						position: absolute;
						top: 11px;
						left: -23px;
						width: 190px;
						height: 69px;
						background: url("../img/landing/distr/oficial/oficial.png");
						content: '';
					}

					&__logo-box {
						display: flex;
						align-items: flex-start;
						justify-content: center;
						height: 96px;
					}
					&__text {

						font-weight: 300;
						font-style: italic;
						font-size: 16px;
						line-height: lh(16, 22);
					}



				}

			}
		}

		.all {
			margin-bottom: 73px;

			&__list {
				margin-top: 65px;

				display: flex;
				flex-wrap: wrap;
				gap: 70px;
				.item {
					flex-basis: 100px;


					&__logo-box {
						display: flex;
						align-items: center;
						justify-content: center;
						max-height: 43px;
					}
					&__text {
						margin-top: 24px;
						font-weight: 300;
						font-style: italic;
						font-size: 16px;
						line-height: lh(16, 22);
					}



				}

			}
		}

		.btn {
			max-width: 369px;
			font-size: 20px;
			font-weight: 400;
			display: block;
			margin: 0 auto;
		}

	}

	.advantages {
		padding-top: 68px;
		padding-bottom: 67px;
		margin-bottom: 170px;
		background: url("../img/landing/2-screen-bg.jpg") center center/cover no-repeat;
		color: #fff;
		position: relative;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: rgba(#000, .5);
			content: '';
			z-index: 0;
		}

		h2 {
			position: relative;
			margin-bottom: 90px
		}



		.btn {
			max-width: 369px;
			font-size: 20px;
			font-weight: 400;
			display: block;
			margin: 0 auto;
			position: relative;
		}

		&__list {
			display: grid;
			grid-template-columns: 1fr 1fr;
			row-gap: 120px;
			position: relative;
			max-width: 1130px;
			margin: 0 auto 100px auto;
			justify-items: center;

			.item {
				max-width: 400px;
				display: flex;
				gap: 22px;
				align-items: center;
				&__num {
					font-weight: 700;
					font-style: italic;
					font-size: 96px;

				}


				&__text {
					font-weight: 500;
					font-size: 20px;
					line-height: lh(20, 27);
				}



			}

		}
	}

	.start-work {
		margin-bottom: 220px;
		h2 {
			margin-bottom: 155px;
		}
		&__list {
			display: flex;
			flex-direction: column;
			max-width: 1197px;
			margin: 0 auto;

			.item {
				font-size: 24px;
				&__title {
					font-weight: 600;

					line-height: lh(24, 20);
					.num {
						background-color: $accent;
						min-height: 50px;
						min-width: 50px;
						display: inline-flex;
						align-items: center;
						justify-content: center;
						border-radius: 100%;
						margin-right: 25px;
					}
					.small {
						font-size: 20px;
						font-weight: 300;
						padding: 0 24px;
					}
					.orange {
						color: #FFCB00;
						text-decoration: underline;
					}

				}
				&__text {
					margin-left: 80px;
					font-weight: 300;
					max-width: 546px;
				}
				&:nth-child(2) {
					margin-left: auto;
					margin-top: 120px;
					margin-bottom: 40px;
					position: relative;
					&::before {
						position: absolute;
						top: 0;
						left: 130px;
						width: 127px;
						height: 144px;
						background: url("../img/landing/arr-work-up.png") no-repeat;
						background-size: 100%;
						content: '';
						transform: translateY(-100%);
					}
					&::after {
						position: absolute;
						bottom: 0;
						left: 60px;
						width: 197px;
						height: 157px;
						background: url("../img/landing/arr-work-left.png");
						background-size: 100%;
						content: '';
						transform: translateY(100%);
					}


				}
			}
		}

	}

	.form-landing {
		padding-top: 81px;
		padding-bottom: 140px;
		background: url("../img/landing/form-bg.jpg") center center/cover no-repeat;
		color: #fff;



		&-form {
			max-width: 1120px;
			margin: 80px auto 0 auto;
			color: #666;
			font-size: 16px;
			display: grid;
			grid-template-columns: repeat(3, 1fr) ;
			flex-wrap: wrap;
			column-gap: 80px;
			row-gap: 51px;
			.placeholder-container {
				::-webkit-input-placeholder {color:#333;}


				&.texarea {
					grid-column: span 3;
					height: 85px;
					textarea {
					}
				}
				input {
					height: 45px;
					&:focus + .caption, &:not(:placeholder-shown) + .caption{
						transform: translate(0, 3px);
						background-color: transparent;
						color: #666;
					}
				}

			}
			.captcha {
				grid-column: span 2;
			}
			.btn {
				align-self: flex-end;
				display: flex;
				align-items: center;
				justify-content: center;
				font-size: 20px;
				font-weight: 400;
				height: 50px;
			}

		}


	}

}
