.checkbox {
	cursor: pointer;

	input {
		pointer-events: none;
		-webkit-appearance: none;
		appearance: none;
		position: absolute;


		&:checked + span {
			&:before {
				background: url("../img/icon/arr/check-black.svg") no-repeat center center;
			}
		}
	}

	span {
		pointer-events: none;
		display: flex;
		align-items: center;
		gap: 10px;
		&:before {
			border: 1px solid #CDCDCD;
			width: 25px;
			height: 25px;
			content: '';
		}

	}

}
