.breadcrumps {
	margin-bottom: 26px;
	display: flex;
	align-items: flex-end;

	a:first-child {
		align-items: center;
		font-size: 32px;
		font-weight: 600;
		color: #000;
		display: flex;
		gap: 15px;
		line-height: 1;
		margin-right: 15px;
	}

	.select {
		color: #666;
		font-size: 14px;
		font-weight: 400;
		min-height: 26px;
		display: flex;
		align-items: center;

		&::before {
			width: 8px;
			height: 1px;
			background-color: #999;
			content: '';
		}

		&__header {
			height: 100%;
			width: 100%;
			padding: 0 13px 0 15px;
			gap: 6px;
			background: #FFF;
			border: none;

			&::after {
				min-width: 13px;
				height: 7px;
				background: url("../img/icon/arr/arr-select-filter.svg");
				content: '';
			}
		}

		&__body {
			width: 100%;
			border-radius: 5px;
		}

	}
}

.page-catalog-list {


	.top-part {



	}



	.body {
		display: flex;
		gap: 20px;
		&__filter {
			width: 230px;
			h3 {
				font-size: 14px;
				font-weight: 600;
				line-height: lh(14,26);
				margin-bottom: 14px;
			}

			.checkbox {
				display: block;
				margin-bottom: 12px;
				span {
					gap: 8px;
					&:before {
						width: 20px;
						height: 20px;
						border-radius: 3px;
					}

				}

			}

			.select {
				width: 100%;
				margin-bottom: 11px;

				&.is-active {
					.select__body {
						transform: rotateX(0);
						visibility: visible;
						transition: transform .3s linear;
					}




				}

				&__header {
					height: 40px;
					width: 100%;
					justify-content: space-between;
					border-radius: 5px;
					color: #999;
					border: 1px solid #999999;

					&::after {
						width: 17px;
						height: 9px;
						background: url("../img/icon/arr/arr-filter.svg");
						content: '';
					}
				}

				&__body {
					width: 100%;
					border-radius: 5px;
					opacity: 1;
					padding: 20px 21px 0 21px;
					top: 40px;


					&::after {
						min-width: 17px;
						height: 9px;
						background: url("../img/icon/arr/arr-filter.svg");
						content: '';
					}
					&-item {
						border-bottom: 1px solid #CDCDCD;
						margin-bottom: 11px;
						padding: 0 0 11px 0;
						display: block;
						&:last-child {
							padding: 0;
							border: none;
						}
						&:hover {
							background-color: transparent;
						}

						span {
							pointer-events: none;
							display: flex;
							align-items: center;
							gap: 10px;
							width: 100%;

							&:before {
								transition: all .5s;
								border: 1px solid #CDCDCD;
								border-radius: 3px;
								min-width: 20px;
								height: 20px;
								content: '';
							}

						}
					}
				}



			}
		}
		&__list {

			flex-grow: 1;

			.item-product-line {
				&:first-child {
					border-top: 1px solid #EAEAEA;
				}
				&__spec-box {
					&__quan-box {
						width: 216px;
						.btn {
							background-color: #FFCB00;
						}
					}
				}



			}
		}
	}

	&.product-list--column {
		.body {
			justify-content: space-between;
			&__list {
				display: grid;
				grid-template-columns: repeat(4, 217px);
				justify-content: space-between;
				margin-left: 20px;
				flex-grow: 1;
				gap: 20px;
				margin-bottom: 40px;
				.item-product-line {
					flex-direction: column;
					border: none;
					// max-width: 217px;
					padding: 0;

					&__spec-box {
						margin-left: initial;
					}

					&__img-box {
						width: 100%;
						margin-bottom: 26px;
						margin-left: initial;
					}

					&__descr-box {
						margin: 0;
					}

					&__descr-box__artical {
						order: -1;
						margin-bottom: 3px;
					}
					&__descr-box__text {
						font-size: 14px;
						margin-bottom: 10px;
					}
					&__descr-box__stock-box {
						font-size: 13px;
						margin-bottom: 13px;
					}
					&__spec-box__quan-box {
						justify-content: space-between;
					}


					&__descr-box__stock-box__bar {
						display: none;
					}
					&__descr-box__stock-box__favorites {
						display: none;
					}
					&__spec-box__unit {
						display: none;
					}
					&__descr-box__stock-box__favorites {
						display: none;
					}

				}



			}
		}


	}



}

.top-bar {

	min-height: 30px;
	display: flex;
	align-items: center;
	margin-bottom: 30px;

	&__find{
		color: #666;
	}

	.checkbox {
		margin-left: auto;

		span {
			gap: 8px;
			&:before {
				width: 20px;
				height: 20px;
				border-radius: 3px;
			}
		}

	}

	.select {
		margin-left: 20px;
		&__header {
			border-radius: 5px;
			background: #FFF;
			box-shadow: 0px 0px 8px 2px rgba(0, 0, 0, 0.08);
			border: none;
			&::before {
				width: 15px;
				height: 15px;
				background: url("../img/icon/top-bar/ruble.svg");
				content: '';
			}
			&::after {
				min-width: 13px;
				height: 7px;
				background: url("../img/icon/arr/arr-select-filter.svg");
				content: '';
			}
		}
		font-size: 14px;
		font-weight: 400;
		&__body {
			width: 100%;
			border-radius: 5px;
		}

		&--sort {

			min-width: 171px;
			.select__header {
				width: 100%;
				&::before {
					width: 19px;
					height: 19px;
					background: url("../img/icon/top-bar/sort.svg");
					content: '';
				}

			}
		}

	}

	&__select-view {

		display: flex;
		gap: 12px;
		align-items: center;
		margin-left: 32px;
		&__column {
			width: 18px;
			height: 18px;
			background: url("../img/icon/top-bar/column.svg");
		}

		&__row {
			width: 21px;
			height: 15px;
			background: url("../img/icon/top-bar/row.svg");
		}

	}


}
