.placeholder-container {
	position: relative;




	input, textarea {
		color: inherit;
		width: 100%;
		height: 50px;
		position: relative;
		z-index: 2;
		padding: 20px 14px;
		border: 1px solid #CDCDCD;
		border-radius: 5px;


		&:focus {
			outline: 0;
		}
		&:focus + .caption, &:not(:placeholder-shown) + .caption{
			opacity: 1;
			top: 0;
			padding: 0 15px;
			z-index: 3;
			font-size: 11px;
			transform: translate(0, -50%);
			color: $accent2;
			background-color: #fff;
		}
	}

	textarea {
		height: 100%;
	}

	.caption {
		position: absolute;
		transition: all 1s;
		top: 50%;
		left: 20px;
		z-index: 2;
		transform: translate(0, -50%);
		pointer-events: none;
	}

	.error {
		border-bottom: 1px solid $accent !important;
		border-radius: inherit;
	}

	&__textarea {
		height: 200px;
	}

}
