.btn {
	background-color: $accent2;
	font-size: 16px;
	padding: 7px 8px;
	cursor: pointer;
	transition: 1s;
	border: none;
	border-radius: 5px;
	text-align: center;
	line-height: 1;
	font-weight: 600;
	&:hover {
		opacity: .8;
	}
	&--red {
		background-color: #E43535;
	;
	}
	&--green {
		background-color: #339933;
	}
	&--silver {
		background-color: #D6D6D6;
	}

}
