.page-lk {
	.top {
		padding-left: 300px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		min-height: 82px;
		margin-bottom: 30px;
		h1 {
			font-size: 32px;
			font-weight: 600;
		}
		.btn {
			padding: 13px 36px;
			font-size: 14px;
			font-weight: 400;
		}
		position: relative;
		&::before {
			position: absolute;
			bottom: 0;
			right: 20px;
			width: calc(100% - 320px);
			height: 2px;
			background-color: #C8C8C8;
			content: '';
		}


	}

	.pagination {
		display: flex;
		justify-content: center;
		color: #494F54;
		font-size: 16px;
		font-weight: 500;
		margin-bottom: 65px;

		&__page {
			display: flex;
			align-items: center;
			justify-content: center;
			min-width: 35px;
			height: 35px;
			&.active {
				border-radius: 5px;
				background: #ECECEC;
			}
		}

	}

	.body {
		display: flex;



		&__aside {
			margin-top: -13px;
			max-width: 300px;
			width: 100%;
			.aside-menu__nav {
				color: #333;
				font-size: 16px;
				font-weight: 500;
				a {
					max-width: 230px;
					&:is(.active) {
						border-radius: 5px;
						background: #F4F4F4;
						color: #F90;
						display: flex;
						align-items: center;
						justify-content: space-between;
						gap: 12px;
						&::after {
							width: 7px;
							height: 15px;
							background: url("../img/icon/arr-left-sidebar.svg") no-repeat;
							content: '';
						}
					}

					display: inline-flex;
					min-height: 48px;
					width: 100%;
					align-items: center;
					padding: 0 18px;
				}


			}
		}

		&__main {
			margin-right: 20px;
			width: 100%;
			&__list {
				margin-bottom: 50px;
				.item {
					min-height: 76px;
					display: flex;
					align-items: center;
					border-bottom: 1px solid #EAEAEA;

					&__main-box {
						display: flex;
						color: #999;
						font-size: 14px;
						font-weight: 400;
						&__num {
							text-align: center;
							padding: 0 11px;
						}
						&__name {
							color: #000;
							span {
								color: #999;
							}
						}


					}

					&__right {
						display: flex;
						gap: 17px;
						margin-left: auto;
						&__edit {
							width: 20px;
							height: 20px;
							background: url("../img/icon/edit-lk.svg") no-repeat;
						}
						&__del {
							width: 23px;
							height: 23px;
							background: url("../img/icon/del-lk.svg") no-repeat;

						}
					}
				}
			}
		}

	}

	&--company {
		.top {
			margin-bottom: 0;
		}
	}

	&--order {
		.top:before {
			display: none;
		}
	}

	.lk-field {
		border-radius: 15px;
		border: 1px solid #EAEAEA;
		background: #F4F4F4;
		color: #333;
		font-size: 14px;
		font-weight: 400;
		line-height: 30px;
		margin-bottom: 26px;
		padding: 8px 30px 17px 24px;

		&.show {
			.lk-field-table {
				transform: rotateX(0);
				visibility: visible;
				opacity: 1;
				transition: transform .3s linear;
				position: static;

			}
		}

		&__body {
			display: flex;
			align-items: flex-start;
			padding-bottom: 11px;
			border-bottom: 1px solid #D0D0D0;
			margin-bottom: 11px;

			&-order {
				max-width: 164px;
				flex-grow: 1;
			}
			&-weight {
				max-width: 157px;
				flex-grow: 1;
			}
			&-categories {
				display: flex;
				flex-grow: 1;
				gap: 13px;
				align-items: center;
				&-item {
					border-radius: 5px;
					background: #FFB444;
					padding: 0 10px;
					&.link {
						font-size: 12px;
						text-decoration: underline;
						background-color: transparent;
					}

				}
			}
			&-link {
				color: #06F;
			}


		}

		&__result {
			display: flex;

			&-total {
				padding-right: 27px;
			}

			&-result {
				margin-left: auto;
			}
		}

	}

	.lk-field-table {
		margin-top: 11px;
		margin-bottom: 80px;
		font-size: 14px;
		font-style: normal;
		font-weight: 400;
		line-height: 26px;
		transform: rotateX(-90deg);
		transform-origin: 0 0;
		visibility: hidden;
		position: absolute;




		&__title {
			display: grid;
			grid-template-columns: 22px 1fr 152px 92px 60px;
			color: #999;
			border-bottom: 2px solid #C8C8C8;
			min-height: 33px;
		}

		&__item {
			display: grid;
			grid-template-columns: 22px 1fr 152px 92px 60px;
			color: #333;
			min-height: 77px;
			align-items: center;
			position: relative;
			&::before {
			    position: absolute;
			    bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				height: 1px;
				background-color: #EAEAEA;
				content: '';
				width: 90%;
			}



		}



	}
}
