.page-home {
	.header__row3 {
		background-color: #F8F8F8;
	}

	.main-slider {
		background-color: #F8F8F8;
		padding-bottom: 40px;
		margin-bottom: 100px;


		&__inner {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
		}

		&__box {
		}

		&-slide {
			display: flex;
			justify-content: center;
		}

		// pagination arr
		&-navigation {
			position: absolute;
			display: flex;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			justify-content: space-between;
			align-items: center;
			width: calc(90%);

			// arr
			&__prev, &__next {
				width: 19px;
				height: 34px;
				cursor: pointer;
				&::before {
					width: 19px;
					height: 34px;
					display: inline-block;
					background: url("../img/icon/arr/arr-top-slider.svg");
					content: '';
				}

				// если далее нету слайдов
				&.swiper-button-disabled,
				&.swiper-button-disabled {
					opacity: 0;
					pointer-events: none;
				}
			}

			&__next {
				&::before {
					transform: rotate(-180deg);
				}
			}


		}

	}

	.hits-slider {
		padding-bottom: 133px;
		h2 {
			margin-bottom: 50px;
			display: inline-flex;
			align-items: center;
			span {
				margin: 0 4px;
				position: relative;
				&::before {
					position: absolute;
					top: 50%;
					transform: translatey(-30%) rotate(-4deg);
					left: 0;
					width: 100%;
					height: 3px;
					background-color: #E43535;
					content: '';
				}
			}
			&::after {
				width: 43px;
				height: 48px;
				background: url("../img/icon/fire.svg");
				content: '';
				margin-top: -10px;
			}

		}

		&__inner {
			position: relative;
		}

		&__box {
			max-width: 1291px;
			margin: 0 auto;
		}

		// pagination arr
		&-navigation {
			position: absolute;
			top: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			height: 215px;
			// arr
			&__prev, &__next {
				background: #F4F4F4;
				border: 1px solid #E7E7E7;
				box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
				width: 45px;
				height: 45px;
				cursor: pointer;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 100%;
				&::before {
					width: 11px;
					height: 19px;
					background: url("../img/icon/arr/arr-slider.svg");
					content: '';
				}

				// если далее нету слайдов
				&.swiper-button-disabled,
				&.swiper-button-disabled {
					opacity: 0;
					pointer-events: none;
				}
			}

			&__next {
				&::before {
					transform: rotate(-180deg);
				}
			}


		}
	}

	.water-season {
		margin-bottom: 150px;
		h2 {
			display: flex;
			align-items: center;
			gap: 24px;
			height: 85px;
			margin-bottom: 50px;
			font-size: 48px;
			background-image: linear-gradient(90deg, #015078 0%, #0276B4 100%);
			color: transparent;
			background-clip: text;
			&::after {
				width: 104px;
				height: 85px;
				background: url("../img/icon/swimmer.png");
				content: '';
			}
		}

		&__body {
			background: url("../img/bg-water.jpg") center center/cover no-repeat;
			background-size: 100%;
			padding: 34px;
			position: relative;

			&-list {
				//display: flex;
				//justify-content: space-between;
				margin-bottom: 36px;


			}
			.item-product {
				padding: 0 15px 25px;
				// flex-basis: 316px;
				background-color: #fff;
				border-radius: 15px;


				&-img-box {
					height: 242px;
				}

				&-articale {
					line-height: 1;
					margin-bottom: 8px;
				}

				&-price-box {
					display: flex;
					font-size: 16px;
					justify-content: space-between;
					align-items: center;
					margin-bottom: 23px;


					&__sale {
						display: flex;
						justify-content: space-between;
						min-height: 30px;
						font-size: 16px;
						span {
							display: flex;
							align-items: center;
							gap: 6px;
							background: #339933;
							border-radius: 5px;
							padding: 0 7px;
							font-size: 14px;
							color: #fff;
						}
					}

				}

				&-text {
					padding: 0;
					margin-bottom: 35px;
				}

				&-stock {
					justify-content: flex-start;
					gap: 9px;
					margin-bottom: 20px;
					font-size: 16px;

				}

				&-cart {
					display: flex;
					justify-content: space-between;
					max-width: 229px;
					input {
						padding-left: 20px;
						width: 90px;
						border: 1px solid #CDCDCD;
						border-radius: 5px;

					}

				}
			}

			&-btn {
				font-weight: 700;
				font-size: 30px;
				line-height: 36px;
				padding: 10px 73px;
				position: relative;
				z-index: 1;
				background-color: transparent;
				border: none;
				display: flex;
				min-width: 354px;
				margin: 0 auto;


				&::after {
					position: absolute;
					z-index: -1;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: #fff;
					border-radius: 27.5px;
					content: '';
					transition: all 1s;
				}
				&::before {
					position: absolute;
					z-index: -1;
					top: 10px;
					left: 0;
					width: 100%;
					height: 100%;
					background: #00D1FF;
					border-radius: 27.5px;
					content: '';
				}

				&:hover{
					&:after {
						top: 10px;
					}

				}

			}
		}

		// pagination arr
		.water-season-navigation {
			position: absolute;
			top: 44%;
			left: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;

			// arr
			&__prev, &__next {
				background: #002B4B;
				border: 5px solid #FFF;
				width: 65px;
				height: 65px;
				cursor: pointer;
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 100%;
				&::before {
					width: 20px;
					height: 31px;
					background: url("../img/icon/arr/arr-slider-woter.svg");
					content: '';
				}

				// если далее нету слайдов
				&.swiper-button-disabled,
				&.swiper-button-disabled {
					opacity: 0;
					pointer-events: none;
				}
			}

			&__prev {
				margin-left: -30px;
			}

			&__next {
				margin-right: -30px;
				&::before {
					transform: rotate(-180deg);

				}
			}


		}




	}

