.form {
	// Мои компании изменить
	&-edit-company {
		margin-bottom: 80px;
		max-width: 1120px;
		width: 100%;
		padding: 18px 18px 30px 18px;

		color: #666;
		font-size: 16px;
		display: grid;
		grid-template-columns: repeat(3, 278px) ;
		column-gap: 62px;
		row-gap: 10px;

		border-radius: 15px;
		border: 1px solid #EAEAEA;
		background: #F4F4F4;
		.form-field {
			display: flex;
			flex-direction: column;
			gap: 12px;
			&__caption {
				color: #000;
				font-size: 14px;
				font-weight: 600;
				line-height: lh(14,26);
			}

			.placeholder-container {
				::-webkit-input-placeholder {color:#333;}


				&.texarea {
					grid-column: span 3;
					height: 85px;
					textarea {
					}
				}
				input {
					height: 40px;
					border: 1px solid #EAEAEA;
					&:focus + .caption, &:not(:placeholder-shown) + .caption{
						transform: translate(0, 3px);
						background-color: transparent;
						color: #666;
					}
				}

			}

			.select {

				width: 100%;
				&__header {
					border-radius: 5px;
					border: 1px solid #EAEAEA;
					width: 100%;
					height: 40px;
					background-color: #fff;
					justify-content: space-between;
					font-size: 14px;
					font-weight: 400;
					padding: 8px 17px;
					&::after {
						min-width: 17px;
						height: 9px;
						background: url(../img/icon/arr/arr-select-large.svg);
						content: "";
					}
				}
				&__body {
					width: 100%;
					position: absolute;
					top: 40px;
					border: none;
					border-top: 1px solid #EAEAEA;
					border-radius: 0;
					input {
						border: none;
					}
				}
			}

			.checkbox {
				color: #000;
				font-size: 12px;
				font-weight: 400;
				line-height: lh(12,26);
				input {
					&:checked + span {
						&:before {
							background: url("../img/icon/arr/lk _check_.svg") no-repeat center center;
							background-color: #fff;
						}
					}
				}

				span {
					pointer-events: none;
					display: flex;
					align-items: center;
					gap: 10px;
					&:before {
						border-radius: 3px;
						border: 1px solid #CDCDCD;
						background-color: #fff;
						width: 15px;
						height: 15px;
						content: '';
					}

				}

			}
		}

		&__btns {
			margin-top: 66px;
			display: flex;
			justify-content: space-between;
			.btn {
				color: #000;
				font-size: 14px;
				font-weight: 400;
				line-height: lh(14,26);
				min-width: 120px;
				&:nth-child(1) {
					background: #CDCDCD;
				}

			}

		}
	}

	// Мои заказы
	&-my-order {
		margin-bottom: 43px;
		max-width: 1120px;
		width: 100%;
		padding: 18px 18px 30px 18px;
		font-size: 16px;
		display: flex;
		align-items: flex-end;
		border-radius: 15px;
		border: 1px solid #EAEAEA;
		background: #F4F4F4;

		.form-field {
			display: flex;
			flex-direction: column;
			gap: 12px;
			&:nth-child(1) {
				flex-grow: 1;
				max-width: 240px;
				input {
					max-width: 180px;
				}
			}
			&:nth-child(2) {
				flex-grow: 1;
				max-width: 292px;
				.placeholder-container {
					max-width: 227px;
					display: flex;
					justify-content: space-between;
					input {
						max-width: 100px;
						padding-left: 22px;
					}
				}
			}
			&:nth-child(3) {
				flex-grow: 1;
				max-width: 310px;
				.select {
					max-width: 200px;
				}
			}


			.placeholder-container {
				::-webkit-input-placeholder {color:#666;}
				input {
					height: 38px;
					border: 1px solid #EAEAEA;
					color: #666;
					font-size: 14px;
					font-weight: 400;
					line-height: 26px; /* 185.714% */
				}

			}
			.select {
				width: 100%;
				&__header {
					border-radius: 5px;
					border: 1px solid #EAEAEA;
					width: 100%;
					height: 40px;
					background-color: #fff;
					justify-content: space-between;
					font-size: 14px;
					font-weight: 400;
					padding: 8px 17px;
					&::after {
						min-width: 17px;
						height: 9px;
						background: url(../img/icon/arr/arr-select-large.svg);
						content: "";
					}
				}
				&__body {
					width: 100%;
					position: absolute;
					top: 40px;
					border: none;
					border-top: 1px solid #EAEAEA;
					border-radius: 0;
					input {
						border: none;
					}
				}
			}
		}

		&__btns {
			display: flex;
			justify-content: space-between;
			height: 40px;
			.btn {
				color: #000;
				font-size: 14px;
				font-weight: 400;
				line-height: lh(14,26);
				min-width: 120px;
				&:nth-child(2) {
					background: transparent                                                  ;
				}

			}

		}
	}
}
